/* eslint-disable @typescript-eslint/no-unused-vars */
"use client"

import type React from "react"

import { useState, useEffect, useCallback } from "react"
import { useSearchParams } from "next/navigation"
import { Card, CardHeader, CardContent, CardTitle, CardDescription } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { FileUp, Loader, FileText, FileIcon as FilePresentation, Download, RefreshCw, Upload, CheckCircle2 } from 'lucide-react'
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { Label } from "@/components/ui/label"
import { Separator } from "@/components/ui/separator"

// Define the interface for document objects
interface SubmittedDocument {
  idPFE: number
  idLivrable: 1 | 2
  fichier: string
}

// Define the interface for livrable names mapping
interface LivrableNames {
  [key: number]: string
}

export default function SoumettrePage() {
  const searchParams = useSearchParams()
  const idEtudiant = searchParams.get("idEtudiant") || "4"
  const idGroupe = searchParams.get("idGroupe") || "2"

  const [pdfFile, setPdfFile] = useState<File | null>(null)
  const [presentationFile, setPresentationFile] = useState<File | null>(null)
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [submitSuccess, setSubmitSuccess] = useState(false)
  const [submitError, setSubmitError] = useState<string | null>(null)
  const [submittedDocuments, setSubmittedDocuments] = useState<SubmittedDocument[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [fetchError, setFetchError] = useState<string | null>(null)

  const livrableNames: LivrableNames = {
    1: "Document PDF",
    2: "Présentation",
  }

  const livrableIcons = {
    1: <FileText className="h-5 w-5" />,
    2: <FilePresentation className="h-5 w-5" />,
  }

  const fetchDocuments = useCallback(async () => {
    setIsLoading(true)
    setFetchError(null)
    try {
      const response = await fetch(`http://localhost:5000/api/livrable/documents?idEtudiant=${idEtudiant}`)
      if (response.ok) {
        const data = (await response.json()) as SubmittedDocument[]
        setSubmittedDocuments(data)
      } else {
        setFetchError("Impossible de récupérer les documents")
      }
    } catch (error) {
      setFetchError("Erreur lors de la récupération des documents")
      console.error("Fetch error:", error)
    } finally {
      setIsLoading(false)
    }
  }, [idEtudiant])

  useEffect(() => {
    fetchDocuments()
  }, [fetchDocuments])

  const handlePdfChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files?.[0]) setPdfFile(e.target.files[0])
  }

  const handlePresentationChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files?.[0]) setPresentationFile(e.target.files[0])
  }

  const resetForm = () => {
    setPdfFile(null)
    setPresentationFile(null)
    const pdfInput = document.getElementById("pdf") as HTMLInputElement
    const presentationInput = document.getElementById("presentation") as HTMLInputElement
    if (pdfInput) pdfInput.value = ""
    if (presentationInput) presentationInput.value = ""
  }

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    setSubmitSuccess(false)
    setSubmitError(null)
    setIsSubmitting(true)

    if (!pdfFile || !presentationFile) {
      setSubmitError("Veuillez sélectionner les deux fichiers.")
      setIsSubmitting(false)
      return
    }

    const formData = new FormData()
    formData.append("files", pdfFile)
    formData.append("files", presentationFile)
    formData.append("idEtudiant", idEtudiant)
    formData.append("idGroupe", idGroupe)
    formData.append("livrables", JSON.stringify([1, 2]))

    try {
      const response = await fetch("http://localhost:5000/api/livrable/submit-documents", {
        method: "POST",
        body: formData,
      })

      if (response.ok) {
        setSubmitSuccess(true)
        resetForm()
        fetchDocuments()
      } else {
        const errorText = await response.text()
        setSubmitError(`Erreur lors de la soumission: ${errorText}`)
      }
    } catch (error) {
      console.error("Erreur de connexion:", error)
      setSubmitError("Erreur de connexion au serveur")
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-[#8B4513]/5 to-white p-4 sm:p-8">
      <Card className="w-full max-w-3xl mx-auto border-0 shadow-xl overflow-hidden">
        <CardHeader className="p-6 sm:p-8 bg-white border-b">
          <CardTitle className="text-2xl sm:text-3xl font-bold text-[#8B4513]">
            Soumettre des documents
          </CardTitle>
          <CardDescription className="text-base text-gray-600">
            Soumettez vos documents de projet pour évaluation
          </CardDescription>
        </CardHeader>

        <CardContent className="p-6 sm:p-8 space-y-8 bg-white">
          {submitSuccess && (
            <Alert className="bg-[#8B4513]/10 border-[#8B4513]/20">
              <CheckCircle2 className="h-5 w-5 text-[#8B4513]" />
              <AlertDescription className="text-[#8B4513] ml-2">
                Documents soumis avec succès!
              </AlertDescription>
            </Alert>
          )}

          {submitError && (
            <Alert className="bg-rose-50 border-rose-200">
              <AlertTitle className="text-rose-800 font-medium">Erreur</AlertTitle>
              <AlertDescription className="text-rose-700">
                {submitError}
              </AlertDescription>
            </Alert>
          )}

          <div className="space-y-6">
            <div className="flex items-center justify-between">
              <h2 className="text-xl font-semibold text-gray-900">Documents Soumis</h2>
              <Button
                variant="outline"
                size="sm"
                onClick={fetchDocuments}
                disabled={isLoading}
                className="h-9 hover:bg-[#8B4513]/10 hover:text-[#8B4513] hover:border-[#8B4513]/20"
              >
                <RefreshCw className={`h-4 w-4 mr-2 ${isLoading ? "animate-spin" : ""}`} />
                Actualiser
              </Button>
            </div>

            {isLoading ? (
              <div className="flex items-center justify-center h-32 bg-gray-50 rounded-xl border border-dashed">
                <Loader className="h-5 w-5 animate-spin text-[#8B4513] mr-2" />
                <p className="text-gray-600">Chargement des documents...</p>
              </div>
            ) : fetchError ? (
              <Alert className="bg-rose-50 border-rose-200">
                <AlertDescription className="text-rose-700">{fetchError}</AlertDescription>
              </Alert>
            ) : submittedDocuments.length > 0 ? (
              <div className="grid gap-3">
                {submittedDocuments.map((doc) => (
                  <div
                    key={`${doc.idPFE}-${doc.idLivrable}`}
                    className="flex items-center justify-between p-4 bg-white rounded-xl border border-gray-200 hover:border-[#8B4513]/30 hover:shadow-md transition-all duration-200"
                  >
                    <div className="flex items-center gap-3">
                      <div className="p-2 bg-[#8B4513]/10 rounded-lg text-[#8B4513]">
                        {doc.idLivrable in livrableIcons &&
                          livrableIcons[doc.idLivrable as keyof typeof livrableIcons]}
                      </div>
                      <div className="space-y-1">
                        <span className="font-medium text-gray-900">{livrableNames[doc.idLivrable]}</span>
                        <span className="block text-sm text-[#8B4513] bg-[#8B4513]/10 px-2 py-0.5 rounded-full w-fit">
                          Soumis
                        </span>
                      </div>
                    </div>
                    <a
                      href={`http://localhost:5000${doc.fichier}`}
                      download
                      className="flex items-center px-4 py-2 text-[#8B4513] hover:text-[#8B4513] hover:bg-[#8B4513]/10 rounded-lg transition-colors"
                    >
                      <Download className="h-4 w-4 mr-2" />
                      <span>Télécharger</span>
                    </a>
                  </div>
                ))}
              </div>
            ) : (
              <div className="flex flex-col items-center justify-center h-32 bg-gray-50 rounded-xl border border-dashed">
                <p className="text-gray-600">Aucun document soumis pour le moment.</p>
              </div>
            )}
          </div>

          <Separator className="my-8" />

          <div className="space-y-6">
            <h2 className="text-xl font-semibold text-gray-900">Soumettre de nouveaux documents</h2>
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="space-y-4">
                <div className="space-y-3">
                  <Label htmlFor="pdf" className="text-base flex items-center gap-2 text-gray-700">
                    <FileText className="h-5 w-5 text-[#8B4513]" />
                    Document PDF
                  </Label>
                  <div className="relative group">
                    <input
                      type="file"
                      id="pdf"
                      accept=".pdf"
                      onChange={handlePdfChange}
                      className="block w-full text-sm text-gray-600
                        file:mr-4 file:py-3 file:px-4
                        file:rounded-lg file:border-0
                        file:text-sm file:font-medium
                        file:bg-[#8B4513]/10 file:text-[#8B4513]
                        hover:file:bg-[#8B4513]/20
                        cursor-pointer border-2 border-dashed
                        rounded-xl focus:outline-none
                        focus:border-[#8B4513]/30 focus:ring-2 focus:ring-[#8B4513]/20
                        transition-all duration-200"
                      required
                    />
                    {pdfFile && (
                      <div className="absolute right-3 top-1/2 -translate-y-1/2 bg-[#8B4513]/10 text-[#8B4513] px-3 py-1 rounded-full text-sm border border-[#8B4513]/20">
                        {pdfFile.name.length > 20 ? `${pdfFile.name.substring(0, 20)}...` : pdfFile.name}
                      </div>
                    )}
                  </div>
                </div>

                <div className="space-y-3">
                  <Label htmlFor="presentation" className="text-base flex items-center gap-2 text-gray-700">
                    <FilePresentation className="h-5 w-5 text-[#8B4513]" />
                    Présentation
                  </Label>
                  <div className="relative group">
                    <input
                      type="file"
                      id="presentation"
                      accept=".ppt,.pptx,.key,.pdf"
                      onChange={handlePresentationChange}
                      className="block w-full text-sm text-gray-600
                        file:mr-4 file:py-3 file:px-4
                        file:rounded-lg file:border-0
                        file:text-sm file:font-medium
                        file:bg-[#8B4513]/10 file:text-[#8B4513]
                        hover:file:bg-[#8B4513]/20
                        cursor-pointer border-2 border-dashed
                        rounded-xl focus:outline-none
                        focus:border-[#8B4513]/30 focus:ring-2 focus:ring-[#8B4513]/20
                        transition-all duration-200"
                      required
                    />
                    {presentationFile && (
                      <div className="absolute right-3 top-1/2 -translate-y-1/2 bg-[#8B4513]/10 text-[#8B4513] px-3 py-1 rounded-full text-sm border border-[#8B4513]/20">
                        {presentationFile.name.length > 20
                          ? `${presentationFile.name.substring(0, 20)}...`
                          : presentationFile.name}
                      </div>
                    )}
                  </div>
                </div>
              </div>

              <Button
                type="submit"
                className="w-full h-12 bg-[#8B4513] hover:bg-[#8B4513]/90 text-white font-medium rounded-xl transition-all duration-200 shadow-lg hover:shadow-xl disabled:opacity-50 disabled:cursor-not-allowed"
                disabled={isSubmitting}
              >
                {isSubmitting ? (
                  <>
                    <Loader className="mr-2 h-5 w-5 animate-spin" />
                    Soumission en cours...
                  </>
                ) : (
                  <>
                    <Upload className="mr-2 h-5 w-5" />
                    Soumettre les documents
                  </>
                )}
              </Button>
            </form>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
